.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wrapper_body {
  flex: 1;
  overflow-y: auto;
}
.wrapper_body .title {
  flex: 1;
  display: flex;
}
.wrapper_body .title > div:first-child {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  margin-right: 15px;
}
.wrapper_body .title > div:last-child {
  flex: 0 0 60px;
  font-size: 14px;
  text-align: center;
}
.wrapper_body .title > div:last-child > i {
  font-size: 20px;
}
.wrapper_body .des {
  display: flex;
  padding-bottom: 15px;
  position: relative;
}
.wrapper_body .des::before,
.wrapper_body .des::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_body .des::before {
  display: none;
  top: 0;
}
.wrapper_body .des::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_body .des::before,
  .wrapper_body .des::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_body .des::before,
  .wrapper_body .des::after {
    transform: scaleY(0.33);
  }
}
.wrapper_body .des > ul {
  flex: 0 0 120px;
  max-width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  position: relative;
}
.wrapper_body .des > ul::before,
.wrapper_body .des > ul::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_body .des > ul::before {
  display: none;
  left: 0;
}
.wrapper_body .des > ul::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_body .des > ul::before,
  .wrapper_body .des > ul::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_body .des > ul::before,
  .wrapper_body .des > ul::after {
    transform: scaleX(0.33);
  }
}
.wrapper_body .des > ul > li {
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper_body .des > ul > li:last-child {
  color: #AEAEAE;
}
.wrapper_body .des > div {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  color: #AEAEAE;
}
.wrapper_body .comment_list_header .user_avatar {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.wrapper_body .comment_list_header .user_avatar > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wrapper_body .comment_list_header :global(.am-card-header-extra) {
  flex: initial;
}
.wrapper_body .comment_list_header :global(.am-card-header-extra) > span {
  padding: 6px 12px;
  background-color: #F7F7F7;
}
.wrapper_body .comment_reply_list {
  margin-top: 15px;
  padding-top: 10px;
  position: relative;
}
.wrapper_body .comment_reply_list::before,
.wrapper_body .comment_reply_list::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_body .comment_reply_list::before {
  top: 0;
}
.wrapper_body .comment_reply_list::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_body .comment_reply_list::before,
  .wrapper_body .comment_reply_list::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_body .comment_reply_list::before,
  .wrapper_body .comment_reply_list::after {
    transform: scaleY(0.33);
  }
}
.wrapper_body .comment_reply_list span {
  color: #34A5EA;
}
.wrapper_comment {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  position: relative;
  background-color: #F7F7F7;
}
@media screen and (max-width: 321px) {
  .wrapper_comment {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_comment {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_comment {
    /*大*/
    font-size: 17px;
  }
}
.wrapper_comment::before,
.wrapper_comment::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_comment::before {
  top: 0;
}
.wrapper_comment::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_comment::before,
  .wrapper_comment::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_comment::before,
  .wrapper_comment::after {
    transform: scaleY(0.33);
  }
}
.wrapper_comment > div {
  flex: 1;
  padding: 4px 20px 4px 10px;
  background-color: #FFF;
  color: #AEAEAE;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wrapper_comment > ul {
  flex: 1;
  display: flex;
  color: #AEAEAE;
  text-align: center;
}
.wrapper_comment > ul > li {
  flex: 1;
  padding: 0 10px;
}
.wrapper_comment > ul > li > i {
  margin-right: 2px;
}
.wrapper_comment > ul > li:not(:last-child) {
  position: relative;
}
.wrapper_comment > ul > li:not(:last-child)::before,
.wrapper_comment > ul > li:not(:last-child)::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_comment > ul > li:not(:last-child)::before {
  display: none;
  left: 0;
}
.wrapper_comment > ul > li:not(:last-child)::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_comment > ul > li:not(:last-child)::before,
  .wrapper_comment > ul > li:not(:last-child)::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_comment > ul > li:not(:last-child)::before,
  .wrapper_comment > ul > li:not(:last-child)::after {
    transform: scaleX(0.33);
  }
}
