.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  min-width: 320px;
  max-width: 1024px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.3);
}
.wrapper_box {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: flex;
  background-color: #FFF;
}
.wrapper_box_input {
  flex: 1;
}
.wrapper_box_operate {
  display: flex;
  align-items: center;
  padding: 14px 15px;
  color: #FFCC33;
  position: relative;
  margin-left: 4px;
}
@media screen and (max-width: 321px) {
  .wrapper_box_operate {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_box_operate {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_box_operate {
    /*大*/
    font-size: 17px;
  }
}
.wrapper_box_operate::before,
.wrapper_box_operate::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.wrapper_box_operate::before {
  left: 0;
}
.wrapper_box_operate::after {
  display: none;
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_box_operate::before,
  .wrapper_box_operate::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_box_operate::before,
  .wrapper_box_operate::after {
    transform: scaleX(0.33);
  }
}
